body {
  margin: 0;
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  font-family: "VT323";
  font-size: 40px;
}

@font-face {
  font-family: "VT323";
  src: url("./../../public/VT323-Regular.ttf");
}

.title {
  font-size: 7vh;
}

span {
  font-size: 5vh;
}

@keyframes border_animation {
 0% {
  border: 3px solid lime
 }

 50% {
  border: 3px solid blue;
 }

 100% {
  border: 3px solid lime;
 }
}

button {
  border-radius: 10px;
  border: none;
  background: linear-gradient(45deg, black, black, darkblue);
  border: 3px solid lime;
  color: aquamarine;

  animation: border_animation 5s linear infinite;
}

button:focus {
  background: linear-gradient(45deg, pink, slategray, blue);
  color: black;
}

input {
  border-radius: 10px;
  padding-inline: 10px;
  background: black;
  color: lime;
  border: none;
  outline: none;

  animation: border_animation 5s linear infinite;
}

.container {
  background: linear-gradient(-45deg, #08162c, black);
  color: aqua;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}

.game_field {
  display: flex;
  justify-content: center;
}

.joystick_wrap {
  /* background-color: red; */
  display: flex;
  justify-content: center;
  margin-top: 20%;
  margin-bottom: 20%;
}

#game_cnv {
  background-color: black;
  border-bottom: 3px solid crimson;
  /* border: 3px solid crimson; */
  /* border-radius: 10px; */
  /* margin: 0 auto; */
  /* margin-bottom: 20%; */
  /* margin-top: 5%; */
}

.main_menu_container {
  /* align-items: center; */
}

@keyframes logo_animation {
  0% {
    color: aqua;
  }
  50% {
    color: lime;
  }
  100% {
    color: aqua;
  }
}

#main_manu_logo {
  font-size: 7vh;
  background: linear-gradient(crimson, #050f1e);
  color: aqua;
  font-style: italic;
  /* box-shadow: */
  border-radius: 30px;
  padding-inline: 20px;

  margin-top: 20%;

  animation: logo_animation 5s linear infinite;
}

.main_menu_items_container {
  display: flex;
  flex-direction: column;
}

.main_menu_items_container > button {
  margin-top: 20%;
}

.join_room_container > label {
  padding-top: 3%;
  padding-bottom: 3%;
}

.colors_list_container {
  /* background: red; */
  display: flex;
  justify-content: center;
  flex-direction: row;
  margin-bottom: 10%;
}

.colors_list_container > div {
  background-color: red;
  position: relative;
  margin-inline: 10px;
  border-radius: 100px;
  padding: 10px;
  margin-top: 5%;
}

.selected_color {
  font-weight: bold;
  animation: border_animation 5s linear infinite;
}